.cloud .load {
  @include transition(all 0.3s ease-in);
  opacity: 0;
}

.cloud .loaded {
  @include transition(all 0.3s ease-in);
  opacity: 1;
}

.cloud {

  .hero {
    @include media($s){
      height: 330px;
      background-size: cover;
      background-position: center;
    }
    @include media($l){
      height: 462px;
    }
  }


  .cloud-services {
    .cloud-services__inner-wrapper {
      @include span-columns(13);
      @include shift(1 of 13);
    }
    background-color: $cloud-peach;
    text-align: center;
    padding-top: em(100);
    padding-bottom: em(100);
  }

  .wiring {
    .copy-wrapper {
      @include media($l){
        @include vertical-align;
      }
    }
    .wiring__inner-wrapper {
      @include media($s){
       @include span-columns(13);
       @include shift(1 of 13);
       padding-top: 110px;
       padding-bottom: 110px;
      }
      @include media($l){
        @include span-columns(15);
        @include shift(0);
        padding-top: 110px;
        padding-bottom: 55px;
      }

      .wiring__inner-wrapper_first{
        @include media($s){
          padding-bottom: 80px;
        }
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          padding-bottom: 0px;
        }
        text-align: center;
      }
      .wiring__inner-wrapper_last {
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          @include omega();
        }
      }
    }
  }

  .wireless {
    .copy-wrapper {
      @include media($l){
        @include vertical-align;
      }
    }
    .wireless__inner-wrapper {
      @include media($s){
       @include span-columns(13);
       @include shift(1 of 13);
       // padding-top: 110px;
       padding-bottom: 110px;
      }
      @include media($l){
        @include span-columns(15);
        @include shift(0);
        padding-top: 110px;
        padding-bottom: 55px;
      }
      .wireless__inner-wrapper_first{
        @include media($s){
          padding-bottom: 80px;
        }
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          padding-bottom: 0px;
        }
        text-align: center;
      }
      .wireless__inner-wrapper_last {
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          @include omega();
        }
        text-align: center;
      }
    }
  }

  .cloud {
    // padding-bottom: 110px;
    .copy-wrapper {
      @include media($l){
        @include vertical-align;
      }
    }
    .cloud__inner-wrapper {
      @include media($s){
       @include span-columns(13);
       @include shift(1 of 13);
       // padding-top: 110px;
       padding-bottom: 110px;
      }
      @include media($l){
        @include span-columns(15);
        @include shift(0);
        padding-top: 110px;
        padding-bottom: 110px;
      }
      .cloud__inner-wrapper_first{
        @include media($s){
          padding-bottom: 80px;
        }
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          padding-bottom: 0px;
        }
        text-align: center;
      }
      .cloud__inner-wrapper_last {
        @include media($l){
          @include span-columns(6);
          @include shift(1);
          @include omega();
        }
      }
    }
  }


}